.slideImageBox__image{object-fit:cover;height:100%;width:100%;pointer-events:none}.slideImageBox_after,.slideImageBox_before{display:block;z-index:1;transition:clip-path .2s ease-in-out,z-index .3s ease-in-out}.slideImageBox_after::after,.slideImageBox_before::after{content:"";position:absolute;display:block;z-index:5;font-size:30px;font-weight:700;color:#191617}.slideImageBox_after img,.slideImageBox_before img{filter:grayscale(0.3);transition:filter .1s ease-in-out}@media(hover: hover){.slideImageBox_after:hover,.slideImageBox_before:hover{z-index:2;transition:clip-path .2s ease-in-out,z-index 0s ease-in-out}.slideImageBox_after:hover img,.slideImageBox_before:hover img{filter:grayscale(0)}}@media(hover: none){.slideImageBox_after:active,.slideImageBox_before:active{z-index:2;transition:clip-path .2s ease-in-out,z-index 0s ease-in-out}.slideImageBox_after:active img,.slideImageBox_before:active img{filter:grayscale(0)}}.slideImageBox_before{clip-path:polygon(100% 0, 100% 0, 0 100%, 0 100%, 0 0)}.slideImageBox_before::after{content:"ДО";top:5px;left:10px}@media(hover: hover){.slideImageBox_before:hover{clip-path:polygon(100% 0, 100% 100%, 100% 100%, 0 100%, 0 0)}}@media(hover: none){.slideImageBox_before:active{clip-path:polygon(100% 0, 100% 100%, 100% 100%, 0 100%, 0 0)}}.slideImageBox_after{clip-path:polygon(100% 0, 100% 0, 100% 100%, 0 100%, 0 100%)}.slideImageBox_after::after{content:"ПОСЛЕ";bottom:5px;right:10px}@media(hover: hover){.slideImageBox_after:hover{clip-path:polygon(0 0, 100% 0, 100% 100%, 0 100%, 0 0)}}@media(hover: none){.slideImageBox_after:active{clip-path:polygon(0 0, 100% 0, 100% 100%, 0 100%, 0 0)}}