.counter{overflow:hidden;display:flex;justify-content:space-between;position:relative;min-height:28px;min-width:80px}.counter_button{position:relative;z-index:1;border:none;padding:0 1em;background-color:rgba(0,0,0,0);color:#393939}.counter_button:disabled{opacity:.7}.counter_button::after,.counter_button::before{position:absolute;margin:auto 0;display:block;top:50%;transform:translateY(-50%);font-size:2em;font-weight:400;left:0;right:0}.counter_button___arrow::after{transform:translateY(-51%);content:"<"}.counter_button___arrow::before{transform:translateY(-51%);content:">"}.counter_button___char::after{content:"–";transform:translateY(-58%)}.counter_button___char::before{transform:translateY(-54%);content:"+"}.counter_button___decrement::after{left:7px}.counter_button___decrement::before{display:none}.counter_button___increment::after{display:none}.counter_button___increment::before{right:7px}.counter_input{display:block;box-sizing:border-box;width:100%;margin:0 auto;padding:0;text-align:center;border:none;background:rgba(0,0,0,0);font-size:inherit;font-weight:600;color:#393939}